<template>
  <div class="container-fluid">
    <div class="row">

      <div class="col-12">
        <div class="accordion" id="accordion">
          <div class="card">
            <div class="card-header" id="headingOne">
              <i class="fa fa-wpforms" aria-hidden="true"></i>
              
              <button class="btn btn-primary float-right" type="button" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                <i class="fa fa-bars" aria-hidden="true"></i>
              </button>
            </div>
            <div id="collapseOne" class="collapse show" aria-labelledby="headingOne" data-parent="#accordion">
              <div class="card-body">
                <div class="form-group row">

                  <div class="form-group col-sm-3">
                    <label class="float-left" for="inputText2"></label>
                    <input type="text" class="form-control" id="inputText2" placeholder="" />
                  </div>

                  <div class="form-group col-sm-3">
                    <label class="float-left" for="inputText6"></label>
                    <input type="text" class="form-control" id="inputText6" placeholder="" />
                  </div>
                  <div class="form-group col-sm-3">
                    <label class="float-left" for="inputText3">()</label>
                    <input type="text" class="form-control" id="inputText3" placeholder="" />
                  </div>
                  <div class="form-group col-sm-3">
                    <label class="float-left" for="inputText4">()</label>
                    <input type="text" class="form-control" id="inputText4" placeholder="" />
                  </div>

                  <div class="form-group col-sm-3">
                    <label class="float-left" for="inputText8"></label>
                    <input type="text" class="form-control" id="inputText8" placeholder="" />
                  </div>
                  <div class="form-group col-sm-3">
                    <label class="float-left" for="inputText9"></label>
                    <input type="text" class="form-control" id="inputText9" placeholder="" />
                  </div>

                  <div class="form-group col-sm-6 d-none d-sm-block"></div>

                  <div class="form-group col-sm-3">
                    <label class="float-left" for="inputText"></label>
                    <select id="inputText" name="inputText" class="form-control">
                      <option selected></option>
                      <option></option>
                      <option></option>
                      <option></option>
                      <option></option>
                      <option></option>
                      <option></option>
                      <option></option>
                    </select>
                  </div>

                  <div class="form-group col-sm-3">
                    <label class="float-left" for="inputText5"></label>
                    <select id="inputText5" name="inputText5" class="form-control">
                      <option selected></option>
                      <option></option>
                      <option></option>
                      <option></option>
                      <option></option>
                      <option></option>
                    </select>
                  </div>
                  <div class="form-group col-sm-3">
                    <label class="float-left" for="inputText7"></label>
                    <select id="inputText7" name="inputText7" class="form-control">
                      <option selected></option>
                      <option>DHL</option>
                      <option>DHL()</option>
                      <option></option>
                      <option>EUB</option>
                      <option></option>
                      <option></option>
                      <option></option>
                    </select>
                  </div>
                  <div class="form-group col-sm-3">
                    <label class="float-left" for="inputText7">()</label>
                    <select id="inputText7" name="inputText7" class="form-control">
                      <option selected></option>
                      <option>BBX</option>
                      <option>CNE</option>
                      <option>CNE</option>
                      <option>CNE</option>

                    </select>
                  </div>

                </div>
              </div>
              <div class="col-sm-12 mb-2 text-right">
                <button type="button" class="btn btn-primary"></button>
                <button type="button" class="btn btn-danger"></button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="col-12">
        <div class="card">
          <div class="card-header">
            <i class="fa fa-wpforms" aria-hidden="true"></i>
            
          </div>
          <div class="card-body">
            <div class="row">
              <div class="col-12 mb-4 text-right" style="padding-right: 27px;">
                <button class="btn btn-success col-2"></button>
              </div>
              <div class="col-12">
                <table id="example" cellspacing="0" width="100%" class="table table-striped table-hover table-bordered">
                  <thead>
                    <tr>
                      <th scope="col" class="align-middle checkBoxColumn">#</th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle">?</th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle">)</th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle">Mail</th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle">ID</th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle"></th>
                      <th scope="col" class="align-middle">PickingID</th>
                      <th scope="col" class="align-middle">PrintDate</th>
                    </tr>
                  </thead>
                </table>
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import 'datatables.net'
import 'datatables.net-bs4'
import { language } from '../config/dataTable'
import { apiLogistics2Index } from '../api/api'

export default {
  name: 'logistics2',
  created () {},
  mounted () {
    // dataTables
    ;(function () {
      var receiveAjaxDataList = []
      var sendAjaxDataList = []
      var index = 0

      //
      var dataTableObj = $('#example').DataTable({
        ajax: apiLogistics2Index,
        // ajax: '/Demo/TestLogisticsAPI2',
        scrollX: true,
        bPaginate: false,
        columns: [
          {},
          { data: 'mainData_1' },
          { data: 'mainData_2' },
          { data: 'mainData_3' },
          { data: 'mainData_4' },
          { data: 'mainData_5' },
          { data: 'mainData_6' },
          { data: 'mainData_7' },
          { data: 'mainData_8' },
          { data: 'mainData_9' },
          { data: 'mainData_10' },
          { data: 'mainData_11' },
          { data: 'mainData_12' },
          { data: 'mainData_13' },
          { data: 'mainData_14' },
          { data: 'mainData_15' },
          { data: 'mainData_16' },
          { data: 'mainData_17' },
          { data: 'mainData_18' },
          { data: 'mainData_19' },
          { data: 'mainData_20' },
          { data: 'mainData_21' },
          { data: 'mainData_22' },
          { data: 'mainData_23' },
          { data: 'mainData_24' },
          { data: 'mainData_25' },
          { data: 'mainData_26' }
        ],
        order: [[1, 'asc']],
        columnDefs: [
          {
            targets: 0,
            data: '',
            orderable: false,
            render: function (data, type, row, meta) {
              var tag = (
                '<div class="custom-control custom-checkbox">' +
                '  <input type="checkbox" class="custom-control-input addList" id="{{checkboxId}}" data-index="{{index}}" {{checked}}>' +
                '  <label class="custom-control-label" for="{{checkboxId}}"></label>' +
                '</div>'
              )
                .replace(/{{checked}}/g, 'checked')
                .replace(/{{index}}/g, index)
                .replace(/{{checkboxId}}/g, 'checkbox' + index)

              return tag
            }
          }
        ],
        language: language
        // 'language': dataTablesModule.language()
      })

      // 
      $(document).on('click', '.addList', function (e) {
        var self = e.target
        var index = self.getAttribute('data-index')

        if (self.checked) {
          var pushData = receiveAjaxDataList[index]
          pushData['deleteIndex'] = index
          sendAjaxDataList.push(pushData)
        } else {
          var deleteIndex = receiveAjaxDataList
            .map(function (e) {
              return e.deleteIndex
            })
            .indexOf(index)
          sendAjaxDataList.splice(deleteIndex, 1)
        }
      })
    })()
  }
}
</script>
<style lang="css">
@import 'datatables.net-bs4/css/dataTables.bootstrap4.css';
@import '../assets/dataTables/dataTables.css';
</style>



